<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>
      <div class="right">
        <div class="right-content">
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>新闻管理</span>
            </p>
          </div>
          <!-- 新闻搜索 -->
          <div class="news-search">
            <span>时间</span>
            <el-date-picker
              type="date"
              placeholder="开始时间"
              v-model="search.time"
              @change="dateChange"
            ></el-date-picker>---
            <el-date-picker
              type="date"
              placeholder="结束时间"
              v-model="search.time2"
              @change="dateChange2"
            ></el-date-picker>
            <el-button
              type="primary"
              icon="el-icon-search"
              class="sreach-btn btn"
              @click="searchValue"
            >搜索</el-button>
          </div>

          <!-- 操作 -->
          <div class="operate">
            <el-button
              type="primary"
              icon="el-icon-success"
              class="sreach-btn"
              @click="operate(1)"
            >批量上架</el-button>
            <el-button
              type="primary"
              icon="el-icon-remove"
              class="sreach-btn"
              @click="operate(0)"
            >批量下架</el-button>
            <el-button type="primary" icon="el-icon-delete" class="sreach-btn" @click="delAll">批量删除</el-button>
            <el-button
              type="primary"
              icon="el-icon-circle-plus"
              class="sreach-btn"
              @click="newsAdd"
            >增加</el-button>
          </div>

          <!-- 产品列表 -->
          <el-table
            class="pro-table"
            :data="tableData"
            border
            style="width: 100%"
            ref="newsTable"
            @selection-change="selectChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="id" label="编号" width="80"></el-table-column>
            <el-table-column label="新闻标题">
              <template slot-scope="scope">
                <a
                  :href="RespondURL.url+'/html/newInfo.html?id='+tableData[scope.$index].details_id"
                >{{tableData[scope.$index].name}}</a>
              </template>
            </el-table-column>
            <el-table-column prop="writer" sortable label="作者" width="120"></el-table-column>
            <el-table-column prop="time" sortable label="发布时间" width="120"></el-table-column>
            <el-table-column prop="source" sortable label="来源" width="150"></el-table-column>
            <el-table-column prop="state" sortable label="状态" width="120"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <i class="el-icon-edit-outline" title="编辑" @click="edit(scope.$index)"></i>
                <i class="el-icon-success" title="上架" @click="up(scope.$index)"></i>
                <i class="el-icon-remove" title="下架" @click="down(scope.$index)"></i>
                <i class="el-icon-delete" title="删除" @click="del(scope.$index)"></i>
                <i class="el-icon-folder-add" title="加入轮播" @click="addLunBo(scope.$index)"></i>
              </template>
            </el-table-column>
          </el-table>

          <!-- 分页 -->
          <div class="page">
            <el-pagination
              v-if="pageshow"
              layout="prev, pager, next"
              :total="total"
              @current-change="pageChange"
              @prev-click="pageChange"
              @next-click="pageChange"
              background
              :current-page="page"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "News",
  components: { Left, HeaderContent },
  created() {
    this.getNews(1);
  },
  data() {
    return {
      curid: "1",
      total: 0, // 总个数
      tableData: [], //列表数据
      checked: [], //被选中的数据
      stateArr: ["下架", "上架"], // 状态
      isPull: false,
      search: {
        time: "",
        time2: ""
      },
      page: 1,
      pageshow: true
    };
  },
  methods: {
    dateChange(data) {
      if (data == null) {
        this.search.time = "";
      } else {
        this.search.time = this.$moment(data).format("YYYY-MM-DD");
      }
    },
    dateChange2(data) {
      if (data == null) {
        this.search.time2 = "";
      } else {
        this.search.time2 = this.$moment(data).format("YYYY-MM-DD");
      }
    },
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    // 获取搜索内容
    getSearch(index) {
      if (!this.search.time) {
        this.search.time = "2000-01-01";
      }
      if (!this.search.time2) {
        this.search.time2 = this.$moment(new Date()).format("YYYY-MM-DD");
      }
      this.$ajax
        .post(
          this.RespondURL.url + "Journalism/conditionquery/" + index,
          this.search
        )
        .then(data => {
          data.data.result.list.forEach(val => {
            let num = parseInt(val.state);
            val.state = this.stateArr[num];
          });
          this.tableData = data.data.result.list;
          this.total = data.data.result.total;
        });
    },
    // 搜索
    searchValue() {
      this.page = 1;
      this.pageshow = false;
      this.getSearch(1);
      this.$nextTick(() => {
        this.pageshow = true;
      });
    },
    // 加入轮播
    addLunBo(index) {
      let state = "";
      this.stateArr.forEach((val, num) => {
        if (val == this.tableData[index].state) {
          state = num;
        }
      });
      let obj = {
        name: this.tableData[index].name,
        classify: "新闻",
        state: state,
        details_id: this.tableData[index].details_id,
        fatherid: this.tableData[index].id,
        img: this.tableData[index].img
      };
      this.$ajax
        .post(this.RespondURL.url + "Slideshow/insertslideshow", obj)
        .then(data => {
          if (!data.data.flag) {
            this.$alert("已加入轮播图,请选择其他", "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          } else {
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          }
        });
    },
    // 新闻添加
    newsAdd() {
      this.$router.push("/newsAdd");
    },
    // 选中
    selectChange(data) {
      this.checked = data;
    },
    //编辑
    edit(index) {
      this.$router.push({
        path: "/newsAdd",
        query: {
          id: [this.tableData[index].details_id]
        }
      });
    },
    // 获取新闻
    getNews(sum) {
      this.$ajax
        .get(
          this.RespondURL.url + "JournalismContent/findJournalismlist/" + sum
        )
        .then(data => {
          data.data.result.list.forEach(val => {
            let num = parseInt(val.state);
            val.state = this.stateArr[num];
          });
          this.tableData = data.data.result.list;
          this.total = data.data.result.total;
        });
    },
    // 上架
    up(index) {
      let obj = {
        id: [this.tableData[index].id]
      };
      this.$ajax
        .post(this.RespondURL.url + "Journalism/onthenews", obj)
        .then(data => {
          console.log(data);
          if (data.data.flag) {
            this.tableData[index].state = "上架";
          }
        });
    },
    // 下架
    down(index) {
      let obj = {
        id: [this.tableData[index].id]
      };
      this.$ajax
        .post(this.RespondURL.url + "Journalism/soldout", obj)
        .then(data => {
          if (data.data.flag) {
            console.log(data);
            this.tableData[index].state = "下架";
          }
        });
    },
    // 删除
    del(index) {
      let obj = {
        id: [this.tableData[index].id],
        details_id: [this.tableData[index].details_id]
      };
      this.$confirm("此操作将删除这些新闻, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$ajax
            .post(this.RespondURL.url + "Journalism/deletejournalism", obj)
            .then(data => {
              console.log(data);
              if (data.data.flag) {
                this.tableData.splice(index, 1);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 批量删除
    delAll() {
      if (this.checked.length == 0) {
        return;
      }
      let arr = this.tableData.map(x => x);
      let idArr = [];
      let idArr2 = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
        idArr2.push(val.details_id);
        arr.forEach((value, index) => {
          if (val.id == value.id) {
            arr.splice(index, 1);
          }
        });
      });
      let obj = {
        id: idArr,
        details_id: idArr2
      };
      this.$confirm("此操作将删除这些新闻, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$ajax
            .post(this.RespondURL.url + "Journalism/deletejournalism", obj)
            .then(data => {
              console.log(data);
              if (data.data.flag) {
                this.checked.forEach(val => {
                  arr.forEach((value, index) => {
                    if (val.id == value.id) {
                      arr.splice(index, 1);
                    }
                  });
                });
                this.tableData = arr;
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 批量操作
    operate(index) {
      if (this.checked.length == 0) {
        return;
      }
      let idArr = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
      });
      let obj = {
        id: idArr
      };
      let repURL = "";
      if (index == 1) {
        //上架
        repURL = this.RespondURL.url + "Journalism/onthenews";
      } else {
        //下架
        repURL = this.RespondURL.url + "Journalism/soldout";
      }
      this.$ajax.post(repURL, obj).then(data => {
        if (data.data.flag) {
          this.$alert(data.data.describe, "提示", {
            confirmButtonText: "确定",
            callback: () => {
              this.tableData.forEach(val => {
                this.checked.forEach(e => {
                  if (e.id == val.id) {
                    val.state = this.stateArr[index];
                  }
                });
              });
              //选中清除
              this.checked = [];
              let refsElTable = this.$refs.newsTable;
              refsElTable.clearSelection();
            }
          });
        }
      });
    },
    // 分页
    pageChange(data) {
      this.getNews(data);
    }
  }
};
</script>
<style lang="less">
.news-search {
  padding: 20px 0 20px 20px;
  text-align: left;
  background: #f6fafd;
  span {
    margin-right: 10px;
  }
  .el-date-editor {
    margin-right: 5px;
  }
  .sreach-btn {
    margin: 0 20px;
  }
}
</style>